#templazee-tab-content{

  .wp-block-templazee-tabs{
      display: none;
  }
  .wp-block-templazee-tabs[data-tab-active="active"]{
      display: block;
  }
  .wp-block-templazee-tabs[data-tab-active="inactive"]{
    display: none !important;
}
  .wp-block-templazee-tabs:first-child{
      display: block;
  }

}  

#templazee-tab-item{
  ul{
    list-style: none;
    padding-left: 0px;
    
    display: flex;
    li{
      margin-right: 10px;
      padding: 10px 20px;
      border: 1px solid;

      &:hover {
        padding: 10px 40px !important;
        background-color: #f0f0f0; // Background color on hover
        color: #333; // Text color on hover
      }
    }
  }
  
}